              axios.get(urlKeeps).then(response => {
                  this.usuarios = response.data;
              });
              */

          this.$router.replace('/')
      }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
